import {
  Grid,
  IconButton,
  Typography,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import React from "react";
import useStyles from "./Styles";
import { Telegram } from "@material-ui/icons";

export default function Footer() {
  const theme = useTheme();
  const FooterSize = useMediaQuery(theme.breakpoints.down("xs"));
  const classes = useStyles();
  return (
    <>
      {!FooterSize ? (
        <Grid container justify="space-around" className={classes.Footer}>
          <Grid item>
            <Typography className={classes.FooterTelId}>
              <IconButton
                style={{
                  backgroundColor: "cyan",
                  width: "10px",
                  height: "10px",
                  color: "blue",
                }}
              >
                <Telegram />
              </IconButton>
              <text
                style={{
                  fontSize: "1rem",
                  color: "black",
                }}
              >
                @Mahdi_KS78
              </text>
            </Typography>
          </Grid>
          <Grid item>
            <Typography className={classes.FooterWebDesign}>
              <text
                style={{
                  fontSize: "1rem",
                  color: "black",
                }}
              >
                Web Design by Mahdi-Keshavarz
              </text>
            </Typography>
          </Grid>
        </Grid>
      ) : (
        <Grid className={classes.FooterSmall}>
          <Typography className={classes.FooterTelId}>
            <IconButton
              style={{
                backgroundColor: "cyan",
                color: "blue",
                width: "10px",
                height: "10px",
              }}
            >
              <Telegram />
            </IconButton>
            <text
              style={{
                fontSize: "1rem",
                color: "black",
                fontFamily: "serif",
              }}
            >
              @Mahdi_KS78
            </text>
          </Typography>
          <Typography className={classes.FooterWebDesign}>
            <text
              style={{
                fontSize: "1rem",
                color: "black",
              }}
            >
              Web Design by Mahdi-Keshavarz
            </text>
          </Typography>
        </Grid>
      )}
    </>
  );
}
